$light: rgba(218, 237, 251, 1);
$light-t: rgba(218, 237, 251, 0.9);
$error: rgba(212, 66, 53, 1);
$dark: rgba(89, 98, 85, 1);
$secondary: rgb(10, 84, 255);
$primary: rgb(15, 26, 67);
$secondary-t: rgba(10, 84, 255, 0.95);
$primary-t: rgba(15, 26, 67, 0.93);

#home {
    background-image: linear-gradient(70deg, rgba(160, 20, 40, 0.2), rgba(0,0,10,1)), url('../media/pic/welcome-background.jpg');
    
    #intro {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        min-height: 100vh;

        padding: 30px 0;
        
        .container {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: flex-end;    
            width: 100%;
            max-width: 800px;
            height: 450px;

            text-align: right;
            padding: 80px 30px 80px;

            h2 {
                margin-bottom: 20px;

                font-size: 2.4em;
                text-shadow: 3px 3px 5px rgba(0, 0, 0, 0.9);

            }

            p {
                margin-bottom: 20px;

                font-size: 1.4em;
                line-height: 1.8em;
                text-shadow: 3px 3px 5px rgba(0, 0, 0, 0.9);
            }
        }


    }
}